.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* ++++++++++++++++++++++++++++++++ */

html, body {
  height: 100%;
}

/* For mobile phones: */

header {
  height: 70px;
  background-color: rgba(184, 175, 139, 1);
}

nav {
  position: fixed ;
  top: 0px;
  right: 0px;
  left: 0px;
  z-index: 1;
  display: flex; 
  background-color: rgba(184, 175, 139, 0.829);
  opacity: 0.9; 
  justify-content: space-around;
  align-items: center; /*Center Vertically */
  height: 70px;
}

.Nav-logo {
  color: #3a869b;
  font-family: 'Comic Sans MS', 'Comic Sans';
  text-shadow: 2px 2px rgb(109, 34, 34);
  font-size: 20px;
}

a.Nav-link{
  text-decoration: none;
  color: white;
  font-size: 18px;
  font-weight: 600;
  text-shadow: 2px 2px rgb(109, 34, 34);
  /* -webkit-text-stroke: 1px grey; */
}

a.Nav-link:hover {
  color: #61dafb;
  font-weight: 900;
}

/* LANDING IMAGE */

.landingImgContainer {
  width: 100%;
  height: 100%;
  background-color: grey;
  position: relative;
}

img.landingImg {
  /* background-image: url(images/desk.png); */
  width: 100%;
  height: 100%;
  object-fit: cover; 
  display: flex; /*gets rid of extra white spsce at the bottom */
  /* position: relative; */
  /* top: 0px; */
  /* background-size: cover; */
}

div.title{
  font-size: 20px;
  position: absolute;
  top: 80px;
  color: white;
  text-shadow: 3px 3px rgb(109, 34, 34);
  text-align: center;
  margin: auto;
  width: 80%;
  left: 10%;
}



/* ABOUT */
#about {
  background-color: rgba(190, 180, 144, .9);
  display: flex;
  flex-direction: column;  
  justify-content: center; 
  align-items: center; 
  padding-top: 70px;
  /* position: relative;         ????????????*/  
}

.info {
  color: white;
  text-align: center;
  width: 80%;
  font-size: 20px;
  line-height: 140%;
  color: rgb(146, 37, 37);
  text-shadow: 1px 1px rgb(54, 11, 11);
  /* position: relative; */
  /* top: -25px; */
}

.portrait {
  height: 100%;
  border-radius: 35%;
  width: 45%;
  object-fit: cover;
  padding-bottom: 25px;
}

/* PROJECTS */

#projectsWrapper {
  background-color: rgba(190, 180, 144, .9);
  display: flex;
  position: relative;
  flex-direction: column;  
  padding-top: 70px;
}

.projectHeading {
  flex: 0 0 100%;
  text-align: center;
  color: white;
  text-shadow: 3px 3px rgb(109, 34, 34);
}

.cardFrame {
  width: 90%;
  margin: auto; /*centers my cardFrames */
  text-align: center; /*centers text within cardFrames*/
  border-radius: 25%;
  background-color: rgba(71, 155, 167, 0.5);
  margin-bottom: 35px;
}

.projectImg {
  width: 100%;
  border-radius: 30%;
}

.App-link {
  color: black;
  text-decoration: none;
  /* line-height: 180%; */
}


/* SECTION CONTACT */
#contact {
  background-color: rgba(190, 180, 144, .9);
  display: flex;
  justify-content: center;
  padding-top: 60px;

}

#contact ul {
  list-style-type: none;
  
}

#contact li {
  padding: 10px;
}

a.contactLink.email {
  text-decoration: none;
  color: #3a869b;
  font-family: 'Comic Sans MS', 'Comic Sans';
  text-shadow: 1px 1px rgb(109, 34, 34);
  font-size: 35px;
 
}

#contact h3 {
  text-align: center;
  text-decoration: none;
  color: #3a869b;
  font-family: 'Comic Sans MS', 'Comic Sans';
  text-shadow: 1px 1px rgb(109, 34, 34);
  font-size: 35px;
  margin: 0;
 
}




/* not used yet */
