  background-color: rgba(190, 180, 144, .9);
  padding-top: 80px;
  padding-bottom: 60px;
}


.container {
  border-radius: 10px;
  background-color: #edf0d4;
  padding: 10px;
  width: 70%;
  position: relative;
  left: 15%;
}

form {
  width: 100%;
  text-align: center;
}

input[type=text], select, textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid rgb(182, 181, 181);
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 8px;
  margin-bottom: 16px;
  /* resize: vertical; */
}

.messageForm {
  text-align: center;
  color: white;
  text-shadow: 2px 2px rgb(109, 34, 34);
}


/* FOOTER */


/* replaced: */
footer {
  background-color: rgb(173, 165, 132);
  display: flex;
  justify-content: center;
  /* position: fixed ; */
  bottom: 0;
  left: 0;
  right: 0;
  height: 60px;
}
footer ul {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style-type: none;
  flex-wrap: wrap;
  margin: 0;
}

.logo {
  padding: 0px 20px;
  box-sizing: content-box;

}

.endNote {
  flex: 0 0 100%;
  text-align: center;
}

