@media only screen and (min-width: 600px) {
  /* For tablets: */
  /* [class*="cardFrame"] {
    width: 50%;
    background-color: mediumspringgreen;
    float: left;
  } */


  /* NAV BAR */
  .Nav-logo {
    font-size: 30px;
  }
  a.Nav-link{
    font-size: 24px;
   
  }
  
  /* LANDING IMAGE */
  div.title {
    font-size: 35px;
    top: 150px;
  }


  /* ABOUT */
  .portrait {
    padding-bottom: 35px;
  }
  .info {
    width: 85%;
    font-size: 28px;
    line-height: 160%;
    color: rgb(146, 37, 37);
    text-shadow: 1px 1px rgb(54, 11, 11);
  }



  /* PROJECTS */

  .projectHeading {
    font-size: 30px;
  }  

  .cardFrame {
    width: 80%; 
    background-color: rgba(71, 155, 167, 0.5);
    /* margin: 10px; */
  }

  /* CONTACT */
  .messageForm {
    font-size: 28px;
  }

}



/* Larger screens */

@media only screen and (min-width: 992px) {
  
  
  header a.Nav-link{
    /* font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif; */
    font-family: 'Roboto Mono', monospace;
    font-size: 28px;
    /* -webkit-text-stroke: 1px black; */
  }

  /* html {
    width: 100%;
    height: 100%;
  } */
  
  
   /* NAV BAR */
  
  .Nav-logo {
    font-size: 30px;
  }

  /* LANDING IMAGE */
  div.title {
    font-size: 40px;
    top: 220px;
  }

  /* ABOUT */

  #about{
    padding-top: 110px;
  }
  .portrait {
    height: 55vmin;
    border-radius: 35%;
    width: 40%;
    /* box-sizing:s border-box; ????*/

  }
  .info {
    width: 80%;
    align-items: center;
    font-size: 22px;
    line-height: 180%;
    text-shadow: 1px 1px rgb(109, 34, 34);
    
  }

  /* PROJECTS */

  #projectsWrapper {
    flex-direction: row;
    padding-top: 110px;
    flex-wrap:wrap;
  }

